<script setup>
import { ref, computed, onMounted, watch, reactive } from 'vue';

import { BaseTree, Draggable, pro, OpenIcon } from '@he-tree/vue';
import '@he-tree/vue/style/default.css';
import '@he-tree/vue/style/material-design.css';
import { dragContext } from '@he-tree/vue';

import { useArticleStore } from '../store';
const store = useArticleStore();

// ÑÑ‚Ð°Ñ‚ÑŒÑ
watch(
  () => store.article.id,
  () => {
    makeHeTree(store.article.id);
  }
);

// Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ñ‹ ÑƒÐ·Ð»Ð°
watch(
  () => store.article,
  () => {
    if (store?.article?.title && typeof currentNodeNode?.text != undefined) {
      currentNodeNode.text = store?.article?.title;
      currentNodeNode.menuOn = store?.article?.menuOn;
      currentNodeNode.isRoute = store?.article?.isRoute;
      document.title = store.article.title;
    }
  },
  { deep: true }
);

onMounted(() => {
  makeHeTree(store.article.id);
});

// Ð¿Ñ€Ð¾Ð±ÐµÐ¶Ð°Ñ‚ÑŒÑÑ Ð¿Ð¾ Ð´ÐµÑ€ÐµÐ²Ñƒ Ð¸ Ð¿Ñ€Ð¸Ð¼ÐµÐ½Ð¸Ñ‚ÑŒ func Ðº ÐºÐ°Ð¶Ð´Ð¾Ð¼Ñƒ ÑƒÐ·Ð»Ñƒ
// func = (node) => { Ñ‡Ñ‚Ð¾ Ñ‚Ð¾ ÑÐ´ÐµÐ»Ð°Ñ‚ÑŒ Ñ node}
function walkTree(node, func) {
  func(node);
  if (node?.children) {
    node.children.forEach((el) => {
      walkTree(el, func);
    });
  }
}

// Ñ‚ÐµÐºÑƒÑ‰Ð¸Ð¹ ÑƒÐ·ÐµÐ»
let currentNodeNode = null;

// Ñ‚ÐµÐºÑƒÑ‰Ð¸Ð¹ id Ð² Ð´ÐµÑ€ÐµÐ²Ðµ
const currentNodeId = ref(store.article.id);

watch(currentNodeId, () => {
  store.loadRec(currentNodeId.value);
});
// // Ð²ÐµÑ€ÑÐ¸Ñ Ð´Ð»Ñ Ð¿ÐµÑ€ÐµÑÑ‚Ñ€Ð¾Ð¹ÐºÐ¸ Ð´ÐµÑ€ÐµÐ²Ð°
const version = ref(0);
// Ð´ÐµÑ€ÐµÐ²Ð¾
const treeData = ref([]);
// Ð¼ÐµÐ½ÑŽ Ð¿Ð¾ Ð¿Ñ€Ð°Ð²Ð¾Ð¹ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð¼Ñ‹ÑˆÐ¸ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚
const menu = ref();
// Ð¿ÑƒÐ½ÐºÑ‚Ñ‹ Ð¼ÐµÐ½ÑŽ
const items = ref([]);

async function makeHeTree(idArticle) {
  const tree = await apiArt({
    command: 'makeHeTree',
    id: idArticle,
  });
  currentNodeId.value = idArticle;

  //
  treeData.value = tree;

  walkTree(treeData.value[0], (n) => {
    n.lazy = n?.directory && !n?.children ? true : false;
    if (n?.id === idArticle) {
      currentNodeNode = n;
    }
  });
}

async function createRec(node, stat, tree) {
  try {
    const article = await apiArt({
      command: 'createNew',
      id: node.id,
    });
    const newNode = {
      id: article.id,
      text: article.title,
      parentId: article.parentId,
      directory: false,
      npp: node.npp,
      children: [],
    };

    const parent = stat;
    tree.add(newNode, parent, stat.children.length);
    parent.open = true; // Ñ€Ð°ÑÐºÑ€Ñ‹Ñ‚ÑŒ Ñ€Ð¾Ð´Ð¸Ñ‚ÐµÐ»Ñ
    currentNodeId.value = article.id;
    document.showToast('Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¾');
  } catch (error) {
    return;
  }
}

async function deleteRec(node, stat, tree) {
  if (node.id == 1) {
    document.showToast('ÐÐµ ÑÑ‚Ð¾Ð¸Ñ‚ ÑƒÐ´Ð°Ð»ÑÑ‚ÑŒ root');
    return;
  }
  if (!(await document.confirmDialog('Ð£Ð´Ð°Ð»Ð¸Ñ‚ÑŒ ?' + node.id))) return;

  const parent = stat.parent;
  if (!parent) {
    document.showToast('ÐžÑˆÐ¸Ð±ÐºÐ° ÑƒÐ´Ð°Ð»ÐµÐ½Ð¸Ñ parent Ð½Ðµ Ð½Ð°Ð¹Ð´ÐµÐ½', 'error');
  }

  let article;
  try {
    article = await apiArt({
      command: 'deleteById',
      id: node.id,
    });
  } catch (error) {
    return;
  }

  const ok = tree.remove(stat); // true/false
  if (!ok) {
    document.showToast('ÐžÑˆÐ¸Ð±ÐºÐ° ÑƒÐ´Ð°Ð»ÐµÐ½Ð¸Ñ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°, Ð¿ÐµÑ€ÐµÐ·Ð°Ð³Ñ€ÑƒÐ·Ð¸Ñ‚Ðµ', 'error');
    return;
  }

  currentNodeId.value = parent.data.id ?? null; // Ð¿ÐµÑ€ÐµÐ²ÐµÑÑ‚Ð¸ ÑƒÐºÐ°Ð·Ð°Ñ‚ÐµÐ»ÑŒ Ð½Ð° Ñ€Ð¾Ð´Ð¸Ñ‚ÐµÐ»Ñ (ÐµÑÐ»Ð¸ Ð¾Ð½ ÐµÑÑ‚ÑŒ)
  parent.open = true; // Ð½Ð° Ð²ÑÑÐºÐ¸Ð¹ ÑÐ»ÑƒÑ‡Ð°Ð¹ Ñ€Ð°ÑÐºÑ€Ñ‹Ñ‚ÑŒ
  parent.data.directory = article.directory; // Ð¼Ð¾Ð¶ÐµÑ‚ Ð²ÑÐµ Ð´ÐµÑ‚Ð¸ ÑƒÐ´Ð°Ð»Ð¸Ð»Ð¸ÑÑŒ
  parent.data.text = article.title; // Ð¾Ð±Ð½Ð¾Ð²Ð¸Ñ‚ÑŒ Ð½Ð°Ð·Ð²Ð°Ð½Ð¸Ðµ
  tree.openNodeAndParents(parent); // Ð³Ð°Ñ€Ð°Ð½Ñ‚Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ Ð²Ð¸Ð´Ð¸Ð¼Ð¾ÑÑ‚ÑŒ
  document.showToast('Ð£Ð´Ð°Ð»ÐµÐ½Ð¾');
}
// Ð¿Ð¾ Ð¿Ñ€Ð¾Ð°Ð²Ð¾Ð¹ ÐºÐ½Ð¾Ð¿ÐºÐµ Ñ„Ð¾Ñ€Ð¼Ð¸Ñ€ÑƒÐµÐ¼ Ð¼ÐµÐ½ÑŽ
function onRightClick(event, node, stat, tree) {
  items.value = [
    {
      label: 'ÐÐ¾Ð²Ð¾Ð¹ Ð²ÐºÐ»Ð°Ð´ÐºÐµ',
      icon: 'fas fa-link',
      command: () => {
        window.open(location.pathname + '#' + node.id, '_blank');
      },
    },

    {
      label: 'Ð¡Ð¾Ð·Ð´Ð°Ñ‚ÑŒ',
      icon: 'fas fa-plus',
      command: () => createRec(node, stat, tree),
    },
    {
      label: 'Ð£Ð´Ð°Ð»Ð¸Ñ‚ÑŒ',
      icon: 'fas fa-trash',
      command: () => deleteRec(node, stat, tree),
    },
    {
      label: 'ÐšÐ¾Ð¿Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ',
      icon: 'fas fa-copy',
      command: () => copyRec(node, stat, tree),
    },
    // ...(node.type == "folder" ? [{ label: "ÐšÐ¾Ð¿Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ Ð’Ð¡Ñ‘", icon: "fas fa-clone", command: () => createEmmit("copyAll", node) }] : [])
  ];
  // Ð¿Ð¾ÐºÐ°Ð· Ð¼ÐµÐ½ÑŽ
  menu.value.show(event);
}

// ÐºÐ¾Ð¿Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ
async function copyRec(node, stat, tree) {
  try {
    const article = await apiArt({
      command: 'copyRec',
      id: node.id,
    });
    const newNode = {
      id: article.id,
      text: article.title,
      parentId: node.id,
      directory: false,
      npp: node.npp,
      children: [],
    };
    const parent = stat.parent;
    tree.add(newNode, parent, node.npp);
    parent.open = true; // Ñ€Ð°ÑÐºÑ€Ñ‹Ñ‚ÑŒ Ñ€Ð¾Ð´Ð¸Ñ‚ÐµÐ»Ñ
    currentNodeId.value = article.id;
    document.showToast('Ð¡ÐºÐ¾Ð¿Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¾');
  } catch (error) {
    return;
  }
}

// Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð½Ð° Ñ‚ÐµÐºÑÑ‚
function onTextClick(node, stat) {
  currentNodeId.value = node.id;
}

// Ð¿Ñ€Ð¸ Ð½Ð°Ð¶Ð°Ñ‚Ð¸Ð¸ Ð½Ð° Ð¿Ð°Ð¿ÐºÑƒ Ñ‡Ð¸Ñ‚Ð°ÐµÐ¼ Ð´ÐµÑ‚ÐµÐ¹ Ð¸ Ð¾Ñ‚ÐºÑ€Ñ‹Ð²Ð°ÐµÐ¼
async function onFolderClick(node, stat, tree) {
  if (node.lazy && !stat?.data?.lazyLoad) {
    stat.data.lazyLoad = true;
    let children;
    try {
      node._loading = true;
      children = await apiArt({ command: 'getChildrens', id: node.id });
      lazyLoad = false;
    } catch (error) {
    } finally {
      node._loading = false;
      stat.data.lazy = false;
    }
    tree.batchUpdate(() => {
      tree.addMulti(children, stat, stat.children.length);
      stat.open = true;
    });
  } else {
    stat.open = !stat.open;
  }
}

// Ð¿ÐµÑ€ÐµÐ¼ÐµÑÑ‚Ð¸Ñ‚ÑŒ ÑƒÐ·ÐµÐ»
let lastPos = null;

function onDragStart() {
  const { dragNode } = dragContext;
  const parent = dragNode.parent ?? null;
  const index = parent ? parent.children.indexOf(dragNode) : 0;
  lastPos = { parent, index };
  currentNodeId.value = dragNode.data.id;
}
//  Ð¿Ð¾ÑÐ»Ðµ Ð¿ÐµÑ€ÐµÐ¼ÐµÑ‰ÐµÐ½Ð¸Ñ
async function onAfterDrop() {
  const { dragNode, targetInfo } = dragContext;
  const id = dragNode.data.id;
  if (id === 1) {
    rollback(dragNode);
    return;
  }
  const parentStat = targetInfo?.parent ?? null;
  const newParentId = parentStat?.data?.id ?? null;
  if (!newParentId) {
    rollback(dragNode);
    return;
  }
  // ÑÐ¾ÑÑ‚Ð°Ð² ÑÐ¾ÑÐµÐ´ÐµÐ¹ Ð¸ Ð¿Ð¾Ð·Ð¸Ñ†Ð¸Ñ
  const list = targetInfo.siblings ?? parentStat.children ?? [];
  const idx = list.findIndex((s) => s === dragNode || s.data?.id === id);
  const idBrotherUp = idx > 0 ? list[idx - 1].data?.id ?? 0 : 0;

  try {
    const res = await apiArt({
      command: 'move',
      id: id,
      newParentId,
      idBrotherUp,
    });
    if (parentStat) parentStat.open = true;
  } catch (e) {
    rollback(dragNode);
    return;
  }
}

function rollback(dragNode) {
  const { targetTree } = dragContext;
  if (lastPos?.parent && targetTree) {
    targetTree.move(dragNode, lastPos.parent, lastPos.index);
    lastPos.parent.open = true;
  }
}
</script>

<template>
  <!-- <pre>{{ JSON.stringify(treeData, null, 2) }} </pre> -->
  <div class="ms-2 tree-pannel">
    <Draggable class="mtl-tree my-2" v-model="treeData" :key="version" @after-drop="onAfterDrop" :ondragstart="onDragStart" treeLine>
      <template #default="{ node, stat, tree }">
        <div class="d-flex pointer" @dragover.stop.prevent="node.lazy ? onFolderClick(node, stat, tree) : ''">
          <div>
            <OpenIcon v-if="stat.children.length" :open="stat.open" class="m-0 p-0" @click.native="stat.open = !stat.open" />
          </div>

          <div v-if="node.lazy" @click.native="onFolderClick(node, stat, tree)">
            <i class="fas fa-chevron-right me-1"></i>
          </div>

          <div
            @contextmenu.prevent="onRightClick($event, node, stat, tree)"
            :class="{
              active: node.id == currentNodeId,
              notroute: !node.isRoute,
              route: node.isRoute,
            }"
            @click="onTextClick(node, stat)"
          >
            <span :style="node.directory ? 'margin-left:-3px' : 'margin-left:3px'"></span>{{ node.text }}

            <i v-if="node.isRoute" class="icon-small fa-link fas mx-1"></i>
            <i v-if="node.menuOn" class="icon-small fas fa-eye mx-1"></i>
          </div>
        </div>
      </template>
    </Draggable>

    <ContextMenu ref="menu" :model="items" />
  </div>
</template>
<style>
.active {
  font-weight: bold;
}

.pointer {
  cursor: pointer;
}

.notroute {
  color: #555;
}

.route {
  color: #007700;
}

.he-tree__open-icon svg {
  width: 20px;
  height: 20px;
}

.he-tree {
  --he-tree-indent: 34px;
  /* ÑÑ‚Ð°Ð½Ð´Ð°Ñ€Ñ‚Ð½Ð¾ Ð¾ÐºÐ¾Ð»Ð¾ 16px */
}

.p-contextmenu ul {
  padding: 0;
}

/* ÑƒÐ±Ð¸Ñ€Ð°ÐµÐ¼ Ð¿Ð¾Ð´Ñ‡ÐµÑ€ÐºÐ¸Ð²Ð°Ð½Ð¸Ðµ */
.p-contextmenu-item-link {
  text-decoration: none !important;
}

.icon-small {
  font-size: 11px;
}
</style>
