import * as hp from 'helper-js';

// support desktop and mobile
const events = {
  start: ["mousedown", "touchstart"],
  move: ["mousemove", "touchmove"],
  end: ["mouseup", "touchend"]
};
const DragEventService = {
  isTouch(e) {
    return e.type && e.type.startsWith("touch");
  },

  _getStore(el) {
    // @ts-ignore
    if (!el._wrapperStore) {
      // @ts-ignore
      el._wrapperStore = [];
    } // @ts-ignore


    return el._wrapperStore;
  },

  on(el, name, handler, options) {
    const {
      args,
      mouseArgs,
      touchArgs
    } = resolveOptions(options);

    const store = this._getStore(el);

    const ts = this;

    const wrapper = function (e) {
      let mouse;
      const isTouch = ts.isTouch(e);

      if (isTouch) {
        // touch
        mouse = {
          x: e.changedTouches[0].pageX,
          y: e.changedTouches[0].pageY,
          pageX: e.changedTouches[0].pageX,
          pageY: e.changedTouches[0].pageY,
          clientX: e.changedTouches[0].clientX,
          clientY: e.changedTouches[0].clientY,
          screenX: e.changedTouches[0].screenX,
          screenY: e.changedTouches[0].screenY
        };
      } else {
        // mouse
        mouse = {
          x: e.pageX,
          y: e.pageY,
          pageX: e.pageX,
          pageY: e.pageY,
          clientX: e.clientX,
          clientY: e.clientY,
          screenX: e.screenX,
          screenY: e.screenY
        };

        if (name === "start" && e.which !== 1) {
          // not left button mousedown
          return;
        }
      }

      return handler.call(this, e, mouse);
    };

    store.push({
      handler,
      wrapper
    }); // follow format will cause big bundle size
    // ä»¥ä¸‹å†™æ³•å°†ä¼šä½¿æ‰“åŒ…å·¥å…·è®¤ä¸ºhpæ˜¯ä¸Šä¸‹æ–‡, å¯¼è‡´æ‰“åŒ…æ•´ä¸ªhp
    // hp.on(el, events[name][0], wrapper, ...args)

    hp.on.call(null, el, events[name][0], wrapper, ...[...args, ...mouseArgs]);
    hp.on.call(null, el, events[name][1], wrapper, ...[...args, ...touchArgs]);
  },

  off(el, name, handler, options) {
    const {
      args,
      mouseArgs,
      touchArgs
    } = resolveOptions(options);

    const store = this._getStore(el);

    for (let i = store.length - 1; i >= 0; i--) {
      const {
        handler: handler2,
        wrapper
      } = store[i];

      if (handler === handler2) {
        hp.off.call(null, el, events[name][0], wrapper, ...[...args, ...mouseArgs]);
        hp.off.call(null, el, events[name][1], wrapper, ...[...args, ...mouseArgs]);
        store.splice(i, 1);
      }
    }
  }

};

function resolveOptions(options) {
  if (!options) {
    options = {};
  }

  const args = options.args || [];
  const mouseArgs = options.mouseArgs || [];
  const touchArgs = options.touchArgs || [];
  return {
    args,
    mouseArgs,
    touchArgs
  };
}

export { DragEventService as default };
